@import url('https://fonts.googleapis.com/css2?family=Anek+Latin:wght@100;200;300;400;500;600;700&display=swap');

:root {

  --theme: #c30b11;
  --grys-500: #272727;
  --grys-400: #707070;
  --grys-300: #888;
  --grys-200: #ececec;
  --grys-100: #fafafa;
  --border: #eee;
  --bg: #f6f6f6;
  --light: #fff;

  --ft-1: 'Anek Latin', sans-serif, helvetica;

  font-family: var(--ft-1);
  font-size: 62.5%;
  font-weight: 400;

  color-scheme: light dark;
  color: var(--grys-300);

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  background: var(--bg);
}

body *{
  font-size: 1rem;
}

.App{
  box-shadow: 0 30px var(--theme) inset;
}

:before,
:after{
  content: '';
  position: absolute;
}

a {
  display: inline-block;
  text-decoration: inherit;
}

a:hover {
  color: #535bf2;
}

span{
  display: inline-block;
}

img{
  width: 100%;
  object-fit: cover;
}

button {
  border-radius: 8px;
  border: none;
  padding: 0.6em 1.2em;
  font-family: inherit;
  cursor: pointer;
}

.container{
  width: 1024px;
  height: auto;
  min-height: 100vh;
  margin: 0 auto;
}