.main_content{
      height: 120px;
      position: relative;
      display: flex;
      gap: 1.5rem;
      justify-content: space-between;
      margin-top: 4.5rem;
}

.box_wrapper{
      width: 100%;
      height: auto;
}

.header_box__content{
      width: 100%;
      height: 38px;
      border-radius: .3rem .3rem 0 0;
      background: var(--grys-100);
      display: flex;
      align-items: center;
      gap: 1rem;
      border: solid 1px var(--border);
      padding: .3rem;
}

.header_content_icon{
      width: 30px;
      height: 100%;
      background: var(--theme);
      color: var(--light);
      border-radius: .3rem;
      display: flex;
      align-items: center;
      justify-content: center;
}

.header_content_icon * {
      color: var(--light);
      font-size: 1.5rem;
}

.header_box__content h3{
      text-transform: uppercase;
      font-weight: 500;
      letter-spacing: .1rem;
}

.box_content{
      background: var(--light);
      padding: 1rem;
      border: solid 1px var(--border);
      border-top: none;
}

.box_content.main{
      padding: .5em;
}