.btn_social{
      display: flex;
      align-items: center;
      color: var(--grys-400);
      gap: .5rem;
      padding: .5rem;
      background: var(--grys-100);
      border-radius: .3rem;
      transition: .2s;
}

.btn_social *{
      font-size: 1.4rem;
}

.btn_social span{
      font-size: 1rem;
      letter-spacing: .1rem;
}

.btn_social:hover{
      background: var(--theme);
      color: var(--light)
}