.main_wrapper{
      flex-grow: 1;
      height: auto;
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
}

.relation_header{
      height: 60px;
      display: flex;
      gap: 1rem;
      align-items: center;
}

.relation_header__avatar{
      width: 50px;
      height: 50px;
      margin: .5rem;
      overflow: hidden;
      border-radius: 50%;
      background: var(--grys-100);
      box-shadow: 0 0 0 .5rem var(--grys-200);
      display: flex;
      align-items: center;
      justify-content: center;
}

.relation_header__avatar img{
      width: 50px;
      height: 50px;
      border-radius: 50%;
}

.relation_header__infor h4{
      text-transform: uppercase;
      font-weight: 600;
}

.relation_description{
      width: calc(100% - 30px);
      margin: 1rem 0 1rem 3rem;
      border-left: solid 1px var(--grys-400);
      padding: .5rem;
}

.relation_description p{
      letter-spacing: .1rem;
}

.relation_wrapper a{
      margin-left: 100%;
      position: relative;
      transform: translatex(-100%);
      text-transform: uppercase;
      color: var(--grys-400);
      border-radius: .3rem;
      padding: .5rem 1.5rem;
      border: solid 1px var(--border);
      letter-spacing: .1rem;
      transition: .2s;
}

.relation_wrapper a:hover{
      background: var(--theme);
      color: var(--light);
      border: solid 1px var(--theme);
}

.video_box{
      width: 100%;
      height: 274px;
}

.video_box iframe{
      border: none;
}

.video_description{
      margin-top: 1rem;
      width: calc(100% - 30px);
      margin-left: 30px;
      border-left: solid 1px var(--grys-400);
      padding: .5rem;
      margin-bottom: 2rem;
}

.video_description p{
      letter-spacing: .1rem;
}

/*  GALLERY */

.modal_content{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      z-index: 999;
      background: rgba(0,0,0,.85);
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
      transition: .2s ease-out;
}

.modal_content.open{
      opacity: 1;
      visibility: visible;
}

.modal_content__img{
      width: 90%;
      height: auto;
      background: #000;
      transform: scale(0);
      opacity: 0;
      transition: .2s .4s ease-out;
}

.modal_content.open .modal_content__img{
      transform: scale(1);
      opacity: 1;
}

.modal_close{
      width: 40px;
      height: 40px;
      background: var(--theme);
      position: absolute;
      right: 50px;
      bottom: 50px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
}

.modal_close *{
      color: var(--light);
      font-size: 1.4rem;
}

.gallery_wrapper{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: .2rem;
}

.gallery_box{
      height: 160px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--light);
      cursor: pointer;
      overflow: hidden;
}

.gallery_box img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      transition: .2s;
}

.gallery_box:hover img{
      opacity: .8;
}

.gallery_description{
      width: calc(100% - 30px);
      padding: .5rem;
      border-left: solid 1px var(--grys-400);
      margin-left: 30px;
      margin-top: 1rem;
      margin-bottom: 2rem;
}

.gallery_description p{
      letter-spacing: .1rem;
}

.footer_content{
      margin-top: 4rem;
      display: flex;
      flex-direction: column;
      padding: 3rem;
      align-items: center;
      gap: .5rem;
}

.footer_content *{
      letter-spacing: .1rem;
}