
.main_aside__right{
      width: 242px;
      height: auto;
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
      flex-shrink: 0;
}

.box_friends{
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
}

.friend_rows{
      width: 100%;
      height: 50px;
      display: flex;
      gap: .5rem;
      align-items: center;
}

.friend_avatar{
      width: 50px;
      height: 50px;
      position: relative;
      background: var(--border);
      border-radius: 50%;
      padding: .4rem;
      flex-shrink: 0;
}

.friend_avatar_img{
      width: 42px;
      height: 42px;
      background: var(--border);
      border-radius: 50%;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.friend_avatar__icon{
      position: absolute;
      right: 0;
      bottom: 0;
}

.friend_avatar__icon *{
      font-size: 1.6rem;
      fill: var(--theme);
}

.friend_data{
      flex-grow: 1;
      display: flex;
      gap: .5rem;
}

.friend_data a{
      text-transform: uppercase;
      color: var(--grys-400);
      letter-spacing: .1rem;
      border-radius: .3rem;
      border: solid 1px var(--border);
      padding: .5rem 1rem;
      transition: .2s;
      margin-top: 1rem;
}

.friend_data a:hover{
      background: var(--theme);
      border: solid 1px var(--theme);
      color: var(--light);
}

.friend_data__infor{
      flex-grow: 1;
}

.friend_data__infor *{
      letter-spacing: .1rem;
}

.friend_data__infor h5{
      font-weight: 600;
      text-transform: uppercase;
}