.load_content{
      width: 100%;
      height: 100vh;
      position: fixed;
      background: var(--bg);
      z-index: 1000;
      display: flex;
      align-items: center;
      justify-content: center;
}

.load_box h1{
      display: flex;
      align-items: center;
      justify-content: center;
      gap: .1rem;
}

.load_box h1 span{
      width: 26px;
      height: 26px;
      background: var(--theme);
      color: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      font-size: 1.4rem;
      position: relative;
      animation: move 2s ease-in-out infinite;
}

@keyframes move{
      0%{
            transform: translateY(0);
      }
      15%{
            transform: translateY(-30px);
      }
      30%{
            transform: translateY(0);
      }
      100%{
            transform: translateY(0);
      }
}