.header_content{
      width: 100%;
      height: 350px;
      display: flex;
      position: relative;
      transform: translateY(3rem);
}

.header_aside{
      width: 206px;
      flex-shrink: 0;
      background: var(--grys-100);
      display: flex;
      flex-direction: column;
}

.header_user{
      width: 100%;
      height: 212px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: .5rem;
}

.user_avatar{
      width: 140px;
      height: 140px;
      clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
      display: flex;
      align-items: center;
      justify-content: center;
}

.header_user h2{
      font-size: 1.8rem;
}

.header_user p{
      text-transform: uppercase;
}

.header_xat{
      width: 100%;
      height: 138px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      border-right: solid 1px var(--border);
      border-bottom: solid 1px var(--border);
}

.header_xat a{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background: var(--light);
      border-left: solid 1px var(--border);
      border-top: solid 1px var(--border);
      color: var(--grys-400);
      gap: .5rem;
      transition: .2s;
}

.header_xat a * {
      font-size: 2rem;
}

.header_xat a span{
      font-size: 1rem;
      text-transform: uppercase;
}

.header_xat a:hover{
      background: var(--theme);
      color: var(--light);
}

.header_banner{
      flex-grow: 1;
      height: 350px;
      overflow: hidden;
}

.banner_content{
      width: 100%;
      height: 100%;
      overflow: hidden;
      background: var(--theme);
}